
  ul {
    list-style-type: none;
    display: flex;
    margin: 0;
    height: 60px;
    background-color: #4E3A2F;
  }
  li {
      flex: 1;  
      padding: 10px 0 10px 40px;   
      border-right: 0.8px solid blanchedalmond;
      margin: 0px;
  }
  #AboutMe {
    margin-left: -50px;
  }
  .nav {
      
      opacity: 0.8;
      width: 100%;
  }
  ul li:hover {
    background: #555;   
  }
   .navheading {
      color: blanchedalmond;
      font-size: 1em;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  
  @media only screen and (max-width: 600px) {
    
    ul {
      list-style-type: none;
      display: grid;
      width: 100%;
      height: 300px;
      margin-left: -50px;
      margin-right: -50px;
    }
    li {
       border-bottom:  0.8px solid blanchedalmond;
    }
    ul li:hover{ 
      display: none;
    }  
          }