body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.professionaldevmaindiv {
    background-color: bisque;
}
body {
    background-color: bisque;
}
.imgcontainer {
    display: flex;
}
.pdpic {
    flex: 1 1;
    width: 20%;
    height: 430px;
}
.profdevtitle {
    text-align: center;
}

  ul {
    list-style-type: none;
    display: flex;
    margin: 0;
    height: 60px;
    background-color: #4E3A2F;
  }
  li {
      flex: 1 1;  
      padding: 10px 0 10px 40px;   
      border-right: 0.8px solid blanchedalmond;
      margin: 0px;
  }
  #AboutMe {
    margin-left: -50px;
  }
  .nav {
      
      opacity: 0.8;
      width: 100%;
  }
  ul li:hover {
    background: #555;   
  }
   .navheading {
      color: blanchedalmond;
      font-size: 1em;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  
  @media only screen and (max-width: 600px) {
    
    ul {
      list-style-type: none;
      display: grid;
      width: 100%;
      height: 300px;
      margin-left: -50px;
      margin-right: -50px;
    }
    li {
       border-bottom:  0.8px solid blanchedalmond;
    }
    ul li:hover{ 
      display: none;
    }  
          }

.container {
    position: relative;
    top: 400px;
    margin: 0;
    height: 100px;
    opacity: 1;
    top: 220px;
    
}

.title {
    color:white;
    text-align: center;
    padding: 20px 0 0 0;
    font-size: 4em;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    position: relative;
    margin: 0;
    opacity: 1;
    top: -10px;
  
}

#titleheading {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #1C4772;
    text-align: center;
    font-size: 3em;
    opacity: 1;
}
.text {
    color:white;
    text-align: center;
    padding: 20px, 20px, 20px, 20px; 
    font-size: 2em;
    
}
.iconholder {
    float: right;
    display: flex;
    width: 53%;
    position: 500px; 
  
}
.icon {
    padding-right: 30px;
    height: 60px;
}
a:hover.iconholder {
    cursor: -webkit-grabbing;
    cursor: grabbing;
}

body {
    background-image: url(/portfolio/static/media/background.a2940ab3.JPG);
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    opacity: 1;
}
.imgetitle{
    font-size: 0.8em;
   padding-left: 50px;
   text-decoration: underline;
}
@media only screen and (max-width: 600px) {
    .coverpic{
        height: 100px;
    }
    .text {
        display: inline;
    }
    .title {
        display: inherit;
    }
    body {
        display: inherit;
        height: 640px;
        width: 360px;
        background-size: contain;
    }
  }
